body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.fixed-top {
    /*background: gold;*/
    /*border: 1px solid black;*/
    width: 100px;
    height: 100px;

    position: absolute;
    left: 100px;
    top: 150px;
}

.fixed-top-2 {
    /*background: gold;*/
    border: 1px solid black;
    width: 100px;
    height: 100px;

    position: absolute;
    left: 100px;
    top: 350px;
}


.text-area {
    width: 95%;
    height: 100%;
    border: 1px solid;
    resize: none;
}

.reactive-textarea {
    width: 97.5%;            
    min-height: 50px;        
    max-height: 300px;       
    overflow-y: hidden;      
    resize: none;            
    padding: 10px;           
    box-sizing: border-box;  
}

.api-call-textarea {
    width: 90%;            
    min-height: 95px;        
    overflow-y: hidden;      
    resize: none;            
    padding: 5px;           
    box-sizing: border-box;  
}

.api-call-button {
    width: 7.5%;            
    min-height: 95px;        
    max-height: 300px;   
    display: inline-block;
    vertical-align: top; 
}

.api-key-textarea {
    width: 97.5%;            
    min-height: 15px;        
    overflow-y: hidden;      
    resize: none;            
    padding: 5px;           
    box-sizing: border-box;  
}

.label-div {
    padding-left: 7px; 
    padding-top: 5px; 
    color: rgb(119, 66, 66);
}


.color-pickas {
    color: rgb(70, 64, 69);
}
